.SectionContainer{
    width: 100vw;
    height: 90vh;
    display: flex;
    justify-content: center;
    overflow-x: hidden;
    overflow-y: hidden;
    margin-top: 6vh;
    margin-bottom: 8vh;
}
.SectionBox{
    width: 100vw;
    height: 100vh;
    display: grid;
    grid-template-rows: auto;
    grid-template-columns: 40%  55%;
    justify-content: space-between;
    align-items: center;
    overflow: hidden;
    color: rgb(104, 100, 100);
    
}
.SectionBox div{
    height: 100vh;
    
}

.SectionDivTexts p{
    margin-top: 2vh;
    margin-bottom: 1vh;
    padding: 15px;
    background-color: white;
    display: block;
    width: 20vw;
    font-size: 1.2em;
    text-align: center;
    cursor: pointer;

}
.SectionDivTexts p:hover{
    background-color: silver;
}
.SectionDivTexts h1{
    text-align: start;
    font-size: 2.1em;
    padding-top: 20px;
    padding-bottom: 20px;
    width: 50vw;
    color: rgb(255, 255, 255);
}
.SectionDivTexts h2{
    width: 48vw;
    font-size: 1.1em;
    text-align: start;

}
.SectionDivTexts .Text{
    font-size: 1.1em;
    padding-top: 20px;

}
.SectionContainerDiv{
    width: 40vw;
    height: auto;
}
.Box{
    display: inline;
    width: 10vw;
    height: 10vh;
}
.SectionDivImg{
    height: 100%;
    background-image: url('https://wallpapercave.com/wp/wp7352290.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    
    
}
.SectionDivImg h1{
    color: white;
    font-family: 'Times New Roman', Times, serif;
    font-size: 2.8em;
    display: block;
}
.SectionDivImg p{
    color: black;
    font-family: 'Times New Roman', Times, serif;
    font-size: 1.2em;
    display: block;
}
/* responsivel */
.SectionDivImgResponsilvel{
    display: none;
}
@media only screen and (max-width: 860px){
    .SectionDivTexts p{
        width: 30vw;
    }
    
    
}

@media only screen and (max-width: 650px){
    .SectionDivImg{
        display: none;
    }
    .SectionDivTexts{
        display: none;
    }
    .SectionDivImgResponsilvel{
        height: 100vh;
        width: 100vw;
        background-image: url('https://wallpapercave.com/wp/wp7352290.jpg');
        background-repeat: no-repeat;
        background-size: cover;
        background-attachment: fixed;
        display: flex;
        justify-content: center;
        align-items: center;
        
    }
    .SectionDivImgResponsilvel div{
        width: 100vw;
        height: 40vh;
        color: white;
    }
    .SectionDivImgResponsilvel h1{
        font-size: 2.6em;
    }
    .SectionDivImgResponsilvel div div{
        
        
        width: 100vw;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .SectionDivImgResponsilvel div div p{
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 15px;
        font-size: 1.2em;
        background-color: white;
        color: black;
        width: 50vw;
        height: 5vh;
        cursor: pointer;
    }
    .SectionDivImgResponsilvel div div p:hover{
        background-color: rgb(51, 48, 48);
    }
}
