section{
    width: 100vw;
    height: 100vh;
    display: grid;
    grid-template-columns: 45% 45%;
    grid-template-rows: auto;
    justify-content: center;
}
.SectionContain{
    margin: 10px;
    height: 90vh;
    cursor: pointer;
    
}
.img{
    height: 100%;
    width: 100%;
    background-image: url("https://wallpapercave.com/wp/wp7774283.jpg");
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    display: flex;
    justify-content: start;
    align-items: end;
}
.SectionContainer{
    height: 90vh;
    display: grid;
    grid-template-rows: 48% 50%;
    grid-template-columns: auto;

    align-content: space-between;
}
.SectionBox{
    height: 100%;
    
}

.img2{
    height: 100%;
    width: 100%;
    background-image: url("https://wallpaperaccess.com/full/1404036.jpg");
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    display: flex;
    justify-content: start;
    align-items: end;
}
.img3{
    height: 100%;
    width: 100%;
    background-image: url("https://www.teahub.io/photos/full/85-859117_nice-houses-on-the-beach-the-best-beaches.jpg");
    background-position: start;
    background-size: cover;
    background-repeat: no-repeat;
    display: flex;
    justify-content: start;
    align-items: end;
    
    
}
.SectionBoxCaixa{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    background-color: rgba(255, 255, 255, 0.295);
    padding: 10px;
    width: 70%;
    border-radius: 20px;
    cursor: pointer;
}
.SectionBoxCaixaGrid span{

    padding: 10px;
    background-color: black;
    color: white;
    
}
.SectionBoxCaixa:hover{
    background-color: white;
    scale: 1;
    transition: 1s;
}
@media only screen and (max-width: 700px){
    section{
        
        height: 160vh;
        display: grid;
        grid-template-rows: 30% 57%;
        grid-template-columns: auto;
        justify-content: space-around;
        align-content: start;
        margin-top: 4vh;
    }
    .SectionContain{
        margin: 10px;
        width: 90vw;
        height: 90vh;
        cursor: pointer;
        
    }
    .img{
    height: 50%;
    width: 100%;
    background-image: url("https://c4.wallpaperflare.com/wallpaper/846/173/87/5c1cbaf96bcec-wallpaper-preview.jpg");
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    display: flex;
    justify-content: start;
    align-items: end;
}
}
@media only screen and (max-width: 450px){
    .SectionContain{
        margin: 10px;
        width: 100vw;
        height: 90vh;
        cursor: pointer;
        
    }
}
