.Conatiner{
    height: 140vh;
    width: 100vw;
    margin-top: 5vh;
    color: white;
    display: grid;
    align-content: top;
    grid-template-rows: 60% 30%;
    background-color: rgba(41, 39, 39, 0.568);

}
.contein{
    height: 40vh;
    display: grid;
    justify-content: center;
    align-items: center;
    text-align: center;

}
.center h2{
    font-size: 2.4em;
    color: rgb(255, 81, 0);
}
.center h1{
    font-size: 3.4em;
    color: black;
}
.center p{
    padding-top: 4vh;
    width: 50vw;
    margin: auto;
    overflow-wrap: break-word;
}
.ConateinImg{
    height: 20vh;
    width: 99vw;
    display: flex;
    justify-content: center;
    align-items: center;

}
.Img{
    width: 125px;
    height: 125px;
    background-image: url("data:image/jpeg;base64,");
    background-position: center;
    background-size: contain;
    border-radius: 100%;
    margin: 25px;
}
.conteinFim{
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.5em;
    color: rgb(209, 200, 200);
    width: 100vw;
    height: 30vh;
    
}
form{
    width: 50vw;
    display: grid;
    grid-template-rows: auto auto auto ;
    justify-content: center;
    text-align: start;
}

form label{
    font-size: 1.5em;
    padding: 5px;
    
}
input{
    height: 4vh;
    margin-bottom: 2vh;
    width: 100%;
}
textarea{
    width: 40vw;
    margin-bottom: 4vh;
    height: 20vh;
    font-size: 1.2em;    

}
@media only screen and (max-width: 800px){
    .conteinFim{
        font-size: 1.1em;
    }
    .Conatiner{
        height: 120vh;
    }
    .center p{
        padding-top: 4vh;
        width: 60vw;
        margin: auto;
    }
}
@media only screen and (max-width: 600px){
    form{
        width: 60vw;
        display: grid;
        grid-template-rows: auto auto auto ;
        justify-content: center;
        text-align: start;
    }
    
    
    textarea{
        width: 100%;
        margin-bottom: 4vh;
        height: 20vh;
        font-size: 1.2em;    
    
    }
}

@media only screen and (max-width: 450px){
    .center h2{
        font-size: 4em;
       
    }
    .center h1{
        font-size: 2em;
        
    }
    .conteinFim{
        font-size: 1.2em;
    }
    .center p{
        font-size: 1.6em;
        padding-top: 4vh;
        width: 80vw;
        margin: auto;
    }
    .Conatiner{
        height: 150vh;
    }
}