.SectionContainer{
    overflow-x: hidden;
    width: 100vw;
    height: 85vh;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-image: url('https://wallpapercave.com/wp/wp3049325.jpg');
    background-attachment: fixed;
    overflow: hidden;
    
}
.SectionContain{
    width: 40vw;
    height: 40vh;
}
.SectionContainerColor{
    position: relative;
    right: 10vw;
    height: 100%;
    width: 110vw;
    overflow-x: hidden;
    background-color: rgba(0, 0, 0, 0.425);
    display: grid;
    background-attachment: fixed;
    justify-content: center;
    align-items: center;
}
.SectionContain h1{
    font-family: 'Times New Roman', Times, serif;

    padding: 10px;
    font-size: 4em;
    color: rgb(255, 255, 255);
}
.SectionContain h3{
    font-family: 'Times New Roman', Times, serif;
    color: rgb(253, 252, 252);
    font-size: 1.7em;
    padding: 20px;
}

@media only screen and (max-width: 700px) {

    .SectionContain{
        width: 100vw;
        height: 40vh;
    }
    .SectionContainerColor{
        position: relative;
        right: 0vw;
        height: 90vh;
        width: 120vw;
        overflow-x: hidden;
        background-color: rgba(0, 0, 0, 0.425);
        display: grid;
        background-attachment: fixed;
        justify-content: center;
        align-items: center;
    }
}