.SectionContainer{
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    overflow-x: hidden;
    overflow-y: hidden;
    margin-top: 6vh;
}
.SectionBox{
    width: 80vw;
    height: 90vh;
    display: grid;
    grid-template-rows: auto;
    grid-template-columns: 50%  45%;
    justify-content: space-between;
    align-items: center;
    overflow: hidden;
    color: rgb(228, 218, 218);
}
.SectionBox div{
    height: 90vh;
}
.SectionDivTextsH1{
    
    color: white;
    font-family: 'Times New Roman', Times, serif;
    font-size: 3.5em;
    display: block;
    margin-top: 4vh;
    margin-bottom: 4vh;
}
.SectionDivTexts p{
    margin-top: 5vh;
    font-size: 1.1em;
    text-align: start;

}

.SectionDivTexts h2{
    
    font-size: 1.4em;
    text-align: start;

}
.SectionDivTexts .Text{
    font-size: 1.1em;
    padding-top: 20px;

}
.SectionDivTexts div{
    
    height: 20vh;
    width: 100%;
    margin-top: 1vh;
    display: grid;
    grid-template-rows: auto auto;
}
.SectionContainerDivBox{
    display: block;
    
}
.SectionContainerDivBox p{
    display: grid;
    align-items: end;
    font-size: 1.3em;
}
.SectionContainerDivBox span{
    margin-top: 9vh;
    background-color: rgb(255, 255, 255);
    cursor: pointer;
    color: black;
    padding: 14px;
    
    font-size: 0.9em;
}
.SectionContainerDivBox span:hover{
    background-color: rgb(185, 182, 177);
}
.SectionContainerDivBox h3{
    font-size: 2.0em;
}
.SectionDivImg{
    height: 100%;
    background-image: url('https://wallpapercave.com/wp/wp7352271.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    
}
.SectionDivImg h1{
    color: white;
    font-family: 'Times New Roman', Times, serif;
    font-size: 2.8em;
    display: block;
}
.SectionDivImg p{
    color: black;
    font-family: 'Times New Roman', Times, serif;
    font-size: 1.2em;
    display: block;
}
@media only screen and (max-width: 800px){
    
    .SectionBox{
        width: 80vw;
        height: 80vh;
        display: grid;
        justify-content: center;
        align-items: center;
    }
    .SectionDivTexts{
        width: 80vw;
    }
    .SectionDivTexts h1{
        text-align: center;
        font-size: 3em;
        padding-top: 40px;
        padding-bottom: 20px;
        width: 80vw;
        color: rgb(255, 255, 255);
    }
    .SectionDivTexts h2{
        width: 78vw;
        text-align: center;
        padding-bottom: 0vh;
        font-size: 1.3em;
    }
    .SectionDivImg{
        display: none;
    }
    .SectionContainerDivBox p{
        display: none;
        align-items: start;
        font-size: 1.7em;
    }
    .SectionContainerDivBox span{
        margin-top: 12vh;
        background-color: white;
        cursor: pointer;
        padding: 14px;
        font-size: 0.8em;
    }
    .SectionDivTexts div{
        background-color: black;
        display: grid;
        grid-template-columns: 55% 35%;
        align-items: center;
        height: 20vh;
        width: 70vw;

        
    }
    
    
    
}
@media only screen and (max-width: 709px){
    .SectionBox{
        width: 80vw;
        height: 100vh;
        display: grid;
        grid-template-rows: auto;
        grid-template-columns: 50%  45%;
        justify-content: space-between;
        align-items: center;
        overflow: hidden;
        color: rgb(228, 218, 218);
    }
    .SectionBox div{
        height: 95vh;
    }
    .SectionContainerDivBox{
        display: none;
    }
    .SectionContainerDivBox span{
        
        width: 40vw;
        font-size: 0.8em;
        position: relative;
        top: -20vh;
    }
}