.HeaderContainer{
    width: 100vw;
    height: 15vh;
    background-color: black;
    display: grid;
    grid-template-columns: auto auto;
    grid-template-rows: auto;
    justify-content: space-around;
    align-items: center;
    overflow-x: hidden;
}
.HeaderLogo{
    font-size: 2.3em;
    font-weight: 400;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    color: white;
}
.HeaderNavOPen{
    display: none;
}
.HeaderNav .a{
    
    padding: 20px;
    margin-left: 5px;
    font-size: 0.8em;
    cursor: pointer;
    text-decoration: none;
    color: rgb(134, 126, 126);   
    
}
.HeaderNav .a:hover{
    color: rgb(214, 212, 205);
}
.HeaderNav .HeaderNavBtn{
    margin-left: 40px;
    color: black;
    padding-left: 20px;
    padding-right: 20px;
    padding-top: 15px;
    padding-bottom: 15px;
    font-size: 1.1em; 
    background-color: rgb(255, 255, 255);
    cursor: pointer;
}
.HeaderNav .HeaderNavBtn:hover{
    color: rgb(15, 11, 11);
    background-color: rgb(190, 183, 183);
}
.HeaderNavOPen .HeaderNavBtn{
    margin-left: 40px;
    color: black;
    padding-left: 20px;
    padding-right: 20px;
    padding-top: 15px;
    padding-bottom: 15px;
    font-size: 1.1em; 
    background-color: rgb(255, 255, 255);
    cursor: pointer;
}
.HeaderNavOPen .HeaderNavBtn:hover{
    color: rgb(15, 11, 11);
    background-color: rgb(190, 183, 183);
}

@media only screen and (max-width: 950px) {
    .HeaderNav a{
        display: none;
    }
    .HeaderNavOPen{
        position: relative;
        top: 5px;
        display: inline;
    }
  }
@media only screen and (max-width: 450px) {
    .HeaderNavOPen .HeaderNavBtn{
        font-size: 0.9em;
        padding: 10px;
        margin-left: -4px;
    }
}